.login-container {
  padding-top: 120px;
}

.login-container .login-bg {
  height: 450px;
  width: 450px;
  margin: 0 auto;
  background: url('../../assets/login-bg.png') no-repeat center;
  background-size: auto 100%;
  position: absolute;
  top: 120px;
  right: 50%;
  z-index: -1;
}

.version-label {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 2px 4px;
  color: lightgray;
  font-size: 8px;
  font-weight: 600;
}



