.app {
  min-width: 100%;
  min-height: 100%;
  padding-top: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.app .page-container {
  padding: 16px;
  padding-top: 112px;
}

.app .fix-section {
  position: fixed;
  top: 48px;
  left: 0;
  right: 0;
  height: 72px;
  z-index: 4;
  padding: 0 16px;
}

.app .fix-table-head {
  position: fixed;
  top: 120px;
  left: 0;
  right: 0;
  height: 40px;
  z-index: 4;
  padding: 0 16px;
}

.app.with-menu {
  padding-top: 88px;
}

.app.with-menu .fix-section {
  top: 88px;
}

.app.with-menu .fix-table-head {
  top: 160px;
}

.app-header {
  background-image: url('../assets/app-header.png');
  background-repeat: no-repeat;
  background-position: center left;
  background-size: auto 100%;
  top: 0;
}

.pointer {
  cursor: pointer;
}

.offscreen {
  position: fixed;
  top: -200%;
  left: -200%;
}

.nooutline {
  outline: none;
}

.x-hide {
  display: none;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

a, a:visited {
  cursor: pointer;
  color: #5197D7;
}

a:hover {
  color: #0059AD;
}

a:active {
  color: #E4F2FF;
}

h2 {
  font-weight: 500;
  text-transform: uppercase;
}

.nopad {
  padding: 0 !important;
}

.pad-h {
  padding: 0 8px;
}

.spacer {
  width: 24px;
}

.align-center {
  text-align: center !important;
}

.align-end {
  text-align: right !important;
}

.col-auto {
  -ms-flex: 0 0 auto !important;
      flex: 0 0 auto !important;
}

.thin-border-v {
  /*width: 1px;
  background: #e5e5e5;
  transform: scaleX(.5);*/
  margin: 0 50px;
}

.input-holder {
  position: relative;
}

.input-holder input {
  padding-left: 24px !important;
  padding-right: 24px !important;
}

.row {
  margin: 0 !important;
}

.row .row {
  margin: 0 -.5rem !important;
}

.row.nomargin {
  margin: 0 !important;
}


.indicator {
  width: 18px;
  height: 18px;
  background: #fff;
  margin: 0 auto;
  border-radius: 50%;
}

.indicator.red {
  background: #FF0204;
}

.indicator.orange {
  background: #F9B444;
}

.indicator.green {
  background: #7ED322;
}

.indicator.gray {
  background: #AAAAAA;
}

.muted {
  color: #858585;
}



