.report-dialog {
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.report-dialog .sidebar {
  -ms-flex-item-align: stretch;
      align-self: stretch;
  padding: 16px 12px;
  border-right: 1px solid #eaeaea;
}

.report-dialog .sidebar .label {
  font-size: 12px;
  font-weight: 500;
  white-space: nowrap;
}

.report-dialog .sidebar .value {
  font-size: 42px;
  font-weight: 200;
  margin-bottom: 22px;
}

.report-dialog .sidebar .smallcaps {
  font-size: 14px;
  font-weight: 300;
  vertical-align: baseline;
}


.report-dialog .table-wrapper,
.report-dialog .table-wrapper > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
}



