.app-menubar {
  z-index: 5;
  -ms-flex-align: stretch;
      align-items: stretch;
  position: fixed;
  top: 48px;
  left: 0;
  right: 0;
  height: 40px;
}

.app-menubar .app-menu-item {
  padding: 0 20px;
  color: #fff;
  line-height: 40px;
  text-decoration: none;
  text-align: center;
  border-right: 1px solid #808080;
}

.app-menubar .app-menu-item.active {
  font-weight: 600;
  color: #F9B444;
  -webkit-box-shadow: 0 4px 0 0 #F9B444;
          box-shadow: 0 4px 0 0 #F9B444;
}



