.dashboard-container {
  padding-top: 72px !important;
  padding-top: 0 !important;
}

.dashboard-container h4 {
  margin: 10px 0;
}

.dashboard-container .grid-chart {
  min-height: 120px;
}

.dashboard-container .grid-chart.line {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.dashboard-container .chart-holder {
  overflow: hidden;
}

.dashboard-container .grid-divider {
  width: 1px;
  background: lightgray;
  margin: 5px 0;
  -webkit-transform: scaleX(.75);
      -ms-transform: scaleX(.75);
          transform: scaleX(.75);
}

.dashboard-container .legend-box {
  padding: 12px 0;
  width: 400px;
  text-align: left;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.dashboard-container .hr {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100vw;
  background: lightgray;
  -webkit-transform: scaleY(.75);
      -ms-transform: scaleY(.75);
          transform: scaleY(.75);
}

.dashboard-container .grid-chart h2 {
  font-size: 18px;
  margin: 0 0 12px;
}

.dashboard-container .legend-box .legend-label {
  font-size: 12px;
  font-weight: 300;
}

.dashboard-container .legend-box .legend-label span {
  display: inline-block;
  width: 8px;
  height: 8px;
  margin-right: 4px;
}

.dashboard-container .legend-box .legend-value {
  font-size: 45px;
  font-weight: 300;
}

.dashboard-container .detail-strip {
  overflow: hidden;
  max-height: 0;
  -webkit-transition: max-height .5s;
  -o-transition: max-height .5s;
  transition: max-height .5s;
}

.dashboard-container .expander {
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  -o-transition: transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}

.dashboard-container .show-detail .detail-strip {
  max-height: 500vh;
}

.dashboard-container .show-detail .expander {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

.dashboard-container .notify-link {
  margin-left: 16px;
}

.dashboard-container .detail-strip label {
  text-transform: uppercase;
  font-size: 10px;
  display: block;
  padding: 0 0 4px;
}



